// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '../static/css/e-icon.css'
import mouse from '../static/js/mouseClick'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

Vue.config.productionTip = false;
Vue.use(ElementUI);
Vue.use(mouse);
Vue.use(mavonEditor);


//ËøõÂ∫¶Êù°
NProgress.inc(0.2);
NProgress.configure({
  easing: 'ease',  // Âä®ÁîªÊñπÂºè
  speed: 500,  // ÈÄíÂ¢ûËøõÂ∫¶Êù°ÁöÑÈÄüÂ∫¶
  showSpinner: false, // ÊòØÂê¶ÊòæÁ§∫Âä†ËΩΩico
  trickleSpeed: 200, // Ëá™Âä®ÈÄíÂ¢ûÈó¥Èöî
  minimum: 0.3 // ÂàùÂßãÂåñÊó∂ÁöÑÊúÄÂ∞èÁôæÂàÜÊØî
})

router.beforeEach((to, from , next) => {
  // ÊØèÊ¨°ÂàáÊç¢È°µÈù¢Êó∂ÔºåË∞ÉÁî®ËøõÂ∫¶Êù°
  NProgress.start();
  // Ë∑ØÁî±ÂèëÁîüÂèòÂåñ‰øÆÊîπÈ°µÈù¢title
  if (to.meta.title) {
    document.title = to.meta.title
  }
  if (to.path.startsWith('/cms')){
    if (!localStorage.getItem('accessToken')){
      next({name: 'login'})
    }
  }
  // Ëøô‰∏™‰∏ÄÂÆöË¶ÅÂä†ÔºåÊ≤°Êúânext()È°µÈù¢‰∏ç‰ºöË∑≥ËΩ¨ÁöÑ„ÄÇËøôÈÉ®ÂàÜËøò‰∏çÊ∏ÖÊ•öÁöÑÂéªÁøª‰∏Ä‰∏ãÂÆòÁΩëÂ∞±ÊòéÁôΩ‰∫Ü
  next();
})

router.afterEach(() => {
  // Âú®Âç≥Â∞ÜËøõÂÖ•Êñ∞ÁöÑÈ°µÈù¢ÁªÑ‰ª∂ÂâçÔºåÂÖ≥Èó≠ÊéâËøõÂ∫¶Êù°
  NProgress.done()
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
})



